<template>
  <div id="console">
    <div>
    {{msg}}
    <h1>{{$t("message.hello")}}</h1>
    </div>
    <h1>{{msg2}}</h1>
    <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    <ve-scatter :data="chartScatterData" :data-zoom="dataZoom" :settings="chartSettings"></ve-scatter>
    <div style="z-index:-1;position: absolute;top:0;width:100%">
    <vue-particles
        color="#dedede"
        :particleOpacity="0.7"
        :particlesNumber="80"
        shapeType="triangle"
        :particleSize="4"
        linesColor="#dedede"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="150"
        :moveSpeed="3"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"/>
    </div>
  </div>
</template>

<script>
import 'echarts/lib/component/dataZoom'
export default {
  name: 'Console',
  data () {
    this.dataZoom = [
      {
        type: 'slider',
        start: 0,
        end: 20
      }
    ]
    this.chartSettings = {
      // yAxisType, // 设置数据类型的
      area: true, // 折线图是否展示为面积图
      tooltipTrigger: 'axis', // 散点图覆盖时通过坐标触发提示框
      dataType: {
        'PV': 'KMB',
        '年龄': 'percent',
        'Order': 'normal'
      },
      labelMap: { // 设置别名
        PV: '访问用户',
        Order: '下单用户'
      },
      metrics: ['PV', 'Order'] // 设置指标 , '年龄'
      // dimension: ['日期'] // 设置维度
    }
    return {
      msg: 'I am console',
      msg2: this.$i18n.t('message.hello'),
      chartData: {
        columns: ['日期', 'PV', 'Order'],
        rows: [
          { '日期': '2018-05-22', 'PV': 32371, 'Order': 19810 },
          { '日期': '2018-05-23', 'PV': 12328, 'Order': 4398 },
          { '日期': '2018-05-24', 'PV': 92381, 'Order': 52910 }
        ]
      },
      chartScatterData: {
        columns: ['日期', 'PV', 'Order', '年龄'],
        rows: {
          '上海': [
            { '日期': '2019/1/1', 'PV': 123, '年龄': 0.3, 'Order': 1244 },
            { '日期': '2019/1/2', 'PV': 1223, '年龄': 6, 'Order': 2344 },
            { '日期': '2019/1/3', 'PV': 7123, '年龄': 0.6901, 'Order': 3245 },
            { '日期': '2019/1/4', 'PV': 4123, '年龄': 12, 'Order': 4355 },
            { '日期': '2019/1/5', 'PV': 3123, '年龄': 15, 'Order': 4564 },
            { '日期': '2019/1/6', 'PV': 2323, '年龄': 20, 'Order': 6537 }
          ],
          '北京': [
            { '日期': '2019/1/1', 'PV': 123, '年龄': 3, 'Order': 1244 },
            { '日期': '2019/1/2', 'PV': 1273, '年龄': 6, 'Order': 2344 },
            { '日期': '2019/1/3', 'PV': 3123, '年龄': 15, 'Order': 4564 },
            { '日期': '2019/1/4', 'PV': 2123, '年龄': 9, 'Order': 3245 },
            { '日期': '2019/1/5', 'PV': 4103, '年龄': 12, 'Order': 4355 },
            { '日期': '2019/1/6', 'PV': 7123, '年龄': 10, 'Order': 3567 }
          ],
          '广州': [
            { '日期': '2019/1/1', 'PV': 123, '年龄': 3, 'Order': 1244 },
            { '日期': '2019/1/2', 'PV': 1223, '年龄': 6, 'Order': 2344 },
            { '日期': '2019/1/3', 'PV': 2123, '年龄': 30, 'Order': 3245 },
            { '日期': '2019/1/5', 'PV': 4123, '年龄': 12, 'Order': 4355 },
            { '日期': '2019/1/4', 'PV': 5123, '年龄': 18, 'Order': 4564 },
            { '日期': '2019/1/6', 'PV': 3843, '年龄': 30, 'Order': 4850 }
          ]
        }
      }
    }
  }
}
</script>



